<template>
<div id="home">
    <header-tab title="可以起名字，但没有动画"/>

    <!-- 轮播图 -->
    <my-swiper :key="homeCarouselList.length" ref="homeSwiper">
        <div class="swiper-slide" v-for="item in homeCarouselList" :key="item.channelid">
            <img :src="item.thumb" class="bg" :alt="item.thumb">
        </div>
    </my-swiper>

    <!-- 菜单分类 -->
    <classify :class="isFixed? 'isFixed': ''"/>
    <keep-alive>
        <router-view/>
    </keep-alive>

    <!-- 实时新闻消息 -->
    <div ref="addHeight" style="padding: 30px;text-align:center;">
        <h3>Real time news - 1</h3>
        <h3>Real time news</h3>
        <h3>Real time news</h3>

        <h3>Real time news - 4</h3>
        <h3>Real time news</h3>
        <h3>Real time news</h3>

        <h3>Real time news - 7</h3>
        <h3>Real time news</h3>
        <h3>Real time news</h3>

        <h3>Real time news - 10</h3>
        <h3>Real time news</h3>
        <h3>Real time news</h3>

        <h3>Real time news - 13</h3>
        <h3>Real time news</h3>
        <h3>Real time news</h3>

        <h3>Real time news - 15</h3>
        <h3>Real time news</h3>
        <h3>Real time news - 18</h3>			
    </div>
</div>
</template>

<script>
const Classify = () => import("@/components/Classify")
const MySwiper = () => import("@/components/Swiper")
const HeaderTab = () => import("@/components/HeaderTab")
export default {
	name: "home",
	data() {
		return {
			homeCarouselList: [],
			isFixed: false
		}
	},
	components: {
		Classify,
        MySwiper,
        HeaderTab,
	},
	mounted() {
		this.getCarouselList()
		window.onscroll = this.getHomeScroll
	},
	beforeDestroy() {
		window.onscroll = null
	},
	methods: {

		// 菜单吸顶
		getHomeScroll() {
			// console.log(document.documentElement.scrollTop, this.$refs.homeSwiper)
			if(document.documentElement.scrollTop>=this.$refs.homeSwiper.$el.offsetHeight) {
				this.isFixed = true
				this.$refs.addHeight.style.paddingTop = "220px"
			} else {
				this.isFixed = false
				this.$refs.addHeight.style.paddingTop = "20px"
			}
		},

		// 获取轮播图
		getCarouselList() {
			this.axios.get("https://api.apiopen.top/musicBroadcasting").then(res => {
				// console.log(res.data)
				let list = res.data.result[0].channellist.filter((item, i) => i < 5)
				// console.log(list);
				if(res.data.code === 200) {
                    this.homeCarouselList = list;
                    this.flag = true
				}
			}).catch(err => {
				console.log(err)
			})
		}
	},
}
</script>

<style scoped>
#home {
	position: relative;
	width: 100%;
	height: 100%;
	background-color: #fcc0c0;
}

#home .swiper-slide {
	width: 100%;
	height: 200px;
}
#home .bg {
	width: 100%;
	height: 100%;
	background: pink center;
}

#home .isFixed {
    position: fixed;
    top: 50px;
}
</style>